@import url(https://fonts.googleapis.com/css2?family=Monda:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app main{margin-top:60px}.app .bottom-text{font-weight:bolder}

.header .logo_image{width:30px;height:30px}

.footer{width:100%;margin-top:50px 0 70px 0;padding:5px;color:wheat;border-top:5px solid teal;font-size:1.2rem;font-weight:bold}

.image-roller .actual-image{height:40vh}@media (max-width: 575.98px){.image-roller .actual-image{height:25vh}}@media (min-width: 576px) and (max-width: 767.98px){.image-roller .actual-image{height:50vh}}

.title-card{font-family:'Monda', sans-serif;margin:20px 0 50px 0}.title-card .title-name{font-size:6rem}.title-card .caption{font-size:2rem}@media (max-width: 575.98px){.title-card{margin:0}.title-card .title-name{padding:10px;font-size:3rem}.title-card .title-name .lastName{display:none}.title-card .caption{font-size:1.2rem}}@media (min-width: 576px) and (max-width: 767.98px){.title-card{padding:10px}.title-card .title-name{font-size:3rem}.title-card .caption{font-size:1.2rem}}

.homepage{display:block}@media (min-width: 576px) and (max-width: 767.98px){.homepage{display:flex;flex-direction:row}.homepage .title-card{width:70%;margin-top:auto}}

.skill-set{margin-top:2rem;display:flex;font-size:7rem;justify-content:space-evenly;flex-wrap:wrap;margin:50px 0 50px 0}.skill-set svg{margin:0 20px 0 20px}@media (max-width: 767.98px){.skill-set{padding:10px;margin:20px 0 20px 0;font-size:3rem}.skill-set svg{margin:10px 5px 0 10px}}

